@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&family=Space+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* setup styles */

a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:after,
blockquote:before,
q:after,
q:before {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}
input[type="search"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  box-sizing: content-box;
}
textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}
audio,
canvas,
video {
  display: inline-block;
  max-width: 100%;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden] {
  display: none;
}
html {
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
a:focus {
  outline: thin dotted;
}
a:active,
a:hover {
  outline: 0;
}
img {
  border: 0;
  -ms-interpolation-mode: bicubic;
}
figure {
  margin: 0;
}
form {
  margin: 0;
}
fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
  white-space: normal;
}
button,
input,
select,
textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}
button,
input {
  line-height: normal;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
  vertical-align: top;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
button,
html,
input,
select,
textarea {
  color: #222;
}
::selection {
  background: #b3d4fc;
  text-shadow: none;
}
img {
  vertical-align: middle;
}
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}
textarea {
  resize: vertical;
}
.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

* {
  box-sizing: border-box;
}

/* setup styles */

html {
  font-size: 62.5%;
}

.wrapper {
  max-width: 1200px;
  width: 100%;
  margin: 0px auto;
}

img {
  width: 100%;
  display: block;
  height: auto;
}

.flexRow {
  display: flex;
  flex-direction: row;
}

.flexColumn {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* typography */

/* google web fonts - Press Start 2P and Space Mono*/

h1 {
  padding: 10px;
  font-size: 3.2rem;
  text-align: center;
  font-family: "Press Start 2P", cursive;
  color: #fafafa;
  text-transform: uppercase;
}

h2,
h3,
p,
ul li {
  font-size: 2rem;
  text-align: center;

  font-family: "Press Start 2P", cursive;
  color: #fafafa;
}

/* styles for the App component */
.appComponent nav h1 {
  background-color: #ff785a;
  color: #fafafa;
}

/* styles for the Login component */

.loginComponent {
  background-color: #ffd25a;
  margin-bottom: 5rem;
}

.loginComponent .instructions {
  padding-bottom: 2rem;
}

.loginComponent ul {
  margin: 6rem;
  list-style-type: circle;
}

.loginComponent ul li {
  color: #191919;
  text-align: left;
  font-size: 2.7rem;
  padding: 5px;
  line-height: 2.9rem;
  letter-spacing: 0.1rem;

  font-family: "Space Mono", monospace;
}

.loginComponent form {
  background-color: #fff05a;
  padding: 3rem;
  width: 100%;
}

.loginComponent input[type="text"] {
  background-color: #fafafa;
  color: #495057;
  padding: 2rem;
  text-align: left;
  font-family: "Press Start 2P", cursive;
  font-size: 1.4rem;
}

.loginComponent label {
  color: #191919;
  font-family: "Press Start 2P", cursive;
  font-size: 2rem;
  padding: 10px;
}

.loginComponent button {
  background-color: #ff785a;
  color: #fafafa;
  padding: 10px 20px;
  font-family: "Press Start 2P", cursive;
  font-size: 4rem;
  -webkit-animation: pulse infinite 4s linear;
          animation: pulse infinite 4s linear;
}

.stylisedButton {
  text-transform: uppercase;
}

.loginComponent input[type="text"] {
  border: 5px solid #ffd25a;
  padding: 15px;
  margin: 2rem;
  font-size: 3rem;
}

/* styling for the timer component */

.timerComponent {
  background-color: #fff05a;
}

.timerComponent button {
  background-color: #ff785a;
  color: #fafafa;
  padding: 10px 20px;
  font-family: "Press Start 2P", cursive;
  font-size: 4rem;
  -webkit-animation: pulse infinite 4s linear;
          animation: pulse infinite 4s linear;
}

.timerComponent .timerDisplay h2,
.timerComponent .messageHolder h3 {
  background-color: #fafafa;
  color: #191919;
  padding: 10px 20px;
  margin: 2rem;
  font-family: "Press Start 2P", cursive;
  font-size: 4.5rem;
}

.timerComponent div {
  padding: 1rem;
}

.timerComponent .messageHolder h3 {
  font-size: 2rem;
}

/* styling for the High Scores page */
.scoreComponent {
  background-color: #ffd25a;
  color: #191919;
}

.scoreComponent h3 {
  color: #191919;
  padding: 1rem;
  text-transform: uppercase;
}

.scoreComponent ul li p,
.scoreComponent p {
  color: #191919;
  padding-left: 3rem;
  font-size: 1.6rem;
  text-align: left;
}

/* pulse animation for the buttons */
@-webkit-keyframes pulse {
  0% {
    transform: scale(0.93);
  }

  70% {
    transform: scale(1);
  }

  100% {
    transform: scale(0.93);
  }
}
@keyframes pulse {
  0% {
    transform: scale(0.93);
  }

  70% {
    transform: scale(1);
  }

  100% {
    transform: scale(0.93);
  }
}

/* media queries begin */

@media (max-width: 920px) {
  .appComponent nav h1 {
    font-size: 2.5rem;
  }

  .loginComponent ul li {
    font-size: 2.3rem;
  }
}

@media (max-width: 700px) {
  .appComponent nav h1 {
    font-size: 2rem;
  }

  .loginComponent ul {
    list-style: none;
  }

  .loginComponent ul li {
    font-size: 2.1rem;
    text-align: center;
  }

  .loginComponent input[type="text"] {
    padding: 1rem;
    font-size: 2rem;
  }
}

@media (max-width: 520px) {
  .appComponent nav h1 {
    font-size: 1.6rem;
  }

  .loginComponent ul li {
    font-size: 1.8rem;
  }

  .loginComponent input[type="text"] {
    padding: 1rem;
  }

  .scoreComponent ul li p,
  .scoreComponent p {
    text-align: center;
  }
}

@media (max-width: 450px) {
  .loginComponent input[type="text"] {
    font-size: 1.6rem;
  }
}

@media (max-width: 370px) {
  .loginComponent input[type="text"] {
    font-size: 1.3rem;
  }

  .loginComponent button {
    font-size: 2.5rem;
  }
}

